.range{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;line-height:15px;border:1px solid #000;background:#fff;color:#000;border-radius:8px;flex:1 1 25%;cursor:pointer}.range--dark{border:1px solid #fff;background:#464646;color:#fff}.range--open{border-bottom:1px solid rgba(0,0,0,0);border-radius:8px 8px 0 0}.range__title{margin:14px 0 15px 14px}.range__сontainer{display:flex;gap:1rem;position:absolute;justify-content:center;align-items:center;align-self:center;width:100%;top:45px;border:1px solid #000;padding:20px 22px;box-sizing:border-box;border-radius:0 0 8px 8px;background:#fff;z-index:1}@media screen and (max-width: 1024px){.range__сontainer{flex-direction:column}}.range__сontainer--dark{border:1px solid #fff;background:#464646;color:#fff}.range__сontainer--open{border-top:none;display:flex}